body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'RobotoNormal';
  src: url(/react-tesla-range-calculator/static/media/Roboto-Regular-webfont.30799efa.eot);
  src: url(/react-tesla-range-calculator/static/media/Roboto-Regular-webfont.30799efa.eot?#iefix) format('embedded-opentype'),
       url(/react-tesla-range-calculator/static/media/Roboto-Regular-webfont.3e5675c8.woff) format('woff'),
       url(/react-tesla-range-calculator/static/media/Roboto-Regular-webfont.7f1320f7.ttf) format('truetype'),
       url(/react-tesla-range-calculator/static/media/Roboto-Regular-webfont.da61d7ef.svg#RobotoRegular) format('svg');
  font-weight: normal;
  font-style: normal;
}
*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font: 300 14px/1.4 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
}
.cf:before,
.cf:after {
    content: '';
    display: table;
}
.cf:after {
    clear: both;
}
.cf {
  *zoom: 1;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.wrapper {
  margin: 100px 0 150px;
}
.header {
    padding: 25px 0;
    text-align: center;
    background: #222;
}

.header img {
    width: 100px;
    height: 13px;
}
.tesla-battery {
  width: 1050px;
  margin: 0 auto;
}
.tesla-battery h1 {
  font-family: 'RobotoNormal';
  font-weight: 100;
  font-size: 38px;
  text-align: center;
  letter-spacing: 3px;
}
.tesla-battery__notice {
    margin: 20px 0;
    font-size: 15px;
    color: #666;
    line-height: 20px;
}
.tesla-car {
  width: 100%;
  min-height: 350px;
  background: #fff url(/react-tesla-range-calculator/static/media/tesla.ff16203f.jpg) no-repeat top center;
  background-size: contain; }

.tesla-wheels {
  height: 247px;
  width: 555px;
  position: relative;
  margin: 0 auto; }

.tesla-wheel {
  height: 80px;
  width: 80px;
  bottom: 0;
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }
  
.tesla-wheel--front {
    left: 53px; }
  
.tesla-wheel--rear {
    right: 72px; }
  
.tesla-wheel--19 {
    background-image: url(/react-tesla-range-calculator/static/media/wheel-19.3b975a00.png);
    -webkit-animation: infinite-spinning 250ms steps(6) infinite;
    animation: infinite-spinning 250ms steps(6) infinite; }
  
.tesla-wheel--21 {
    background-image: url(/react-tesla-range-calculator/static/media/wheel-21.e787dc92.png);
    -webkit-animation: infinite-spinning 480ms steps(12) infinite;
    animation: infinite-spinning 480ms steps(12) infinite; }

@keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }
@-webkit-keyframes infinite-spinning {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }
.tesla-stats {
  margin: -70px 0 30px; }
  .tesla-stats ul {
    text-align: center; }
    .tesla-stats ul li {
      display: inline-block;
      width: 130px;
      position: relative; }
      .tesla-stats ul li p {
        font-size: 40px;
        font-weight: normal;
        font-family: 'RobotoNormal';
        display: block;
        padding: 0 18px 0 0;
        position: relative;
        color: #008dff;
        text-align: right; }
        .tesla-stats ul li p:after {
          font-size: 14px;
          font-weight: normal;
          font-family: 'RobotoNormal';
          content: 'MI';
          position: absolute;
          top: 8px;
          right: 0; }
  .tesla-stats-icon {
    height: 20px;
    background-size: auto 13px;
    background-position: top right;
    background-repeat: no-repeat; }
    .tesla-stats-icon--60 {
      background-image: url(/react-tesla-range-calculator/static/media/60.7ee66eea.svg); }
    .tesla-stats-icon--60d {
      background-image: url(/react-tesla-range-calculator/static/media/60d.246b5ba5.svg); }
    .tesla-stats-icon--75 {
      background-image: url(/react-tesla-range-calculator/static/media/75.37862a62.svg); }
    .tesla-stats-icon--75d {
      background-image: url(/react-tesla-range-calculator/static/media/75d.9c937cb9.svg); }
    .tesla-stats-icon--90d {
      background-image: url(/react-tesla-range-calculator/static/media/90d.28a215f0.svg); }
    .tesla-stats-icon--p100d {
      background-image: url(/react-tesla-range-calculator/static/media/p100d.62ed87b4.svg); }
